import { Component, ElementRef, Input, OnDestroy, OnInit } from '@angular/core'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { of, Subject, Subscription } from 'rxjs'
import { flatMap, switchMap } from 'rxjs/operators'
import { environment } from 'src/environments/environment'
import { IUser, Term } from '../api-interfaces'
import { TermService } from '../services/compliance/term.service'
import { SessionService } from '../services/session.service'

type TermsModalStep = 'terms' | 'privacy' | 'fees'

const ease = require('ease-component')
const scroll = require('scroll')

@Component({
    selector: 'terms-modal',
    templateUrl: 'terms-modal.component.html',
})
export class TermsModalComponent implements OnInit, OnDestroy {
    @Input()
    public step: TermsModalStep = 'terms'
    @Input()
    public viewOnly = false
    public term: Term
    public user?: IUser
    public appName = environment.appName

    public subheader: string

    public isDown = false
    private modalContainer: HTMLElement

    private fetchEvent = new Subject<void>()
    private subscriptions = new Subscription()

    constructor(
        public activeModal: NgbActiveModal,
        public translate: TranslateService,
        private session: SessionService,
        private termsService: TermService,
        private element: ElementRef
    ) {}

    public ngOnInit(): void {
        this.subheader = this.translate.instant('common.you-must-accept-the-terms')
        this.subscriptions.add(
            this.fetchEvent
                .pipe(
                    switchMap(() => (this.session.isAuthenticated() ? this.session.userStream : of(undefined))),
                    flatMap(user => this.termsService.getApplicableTerm(user?.id))
                )
                .subscribe(term => {
                    this.term = term
                    setTimeout(() => this.scrollHandler())
                })
        )
        this.fetchEvent.next()

        this.modalContainer = this.element.nativeElement.querySelector('.terms-container') as HTMLElement
        this.modalContainer.addEventListener('scroll', this.scrollHandler)
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
        this.modalContainer.removeEventListener('scroll', this.scrollHandler)
    }

    public nextStep(): void {
        this.modalContainer.scrollTop = 0

        if (this.step === 'terms') {
            this.step = 'privacy'
        } else if (this.step === 'privacy') {
            this.step = 'fees'
        } else {
            this.activeModal.close()
        }
        this.subheader = `${this.translate.instant('common.you-must-accept-the')} ${
            this.step === 'privacy' ? this.translate.instant('common.privacy-policy') : this.step
        } ${this.translate.instant('common.to-continue')}`
    }

    public scrollDown(): void {
        scroll.top(this.modalContainer, this.modalContainer.scrollHeight - this.modalContainer.clientHeight, {
            duration: 1000,
            ease: ease.inOutExpo,
        })
    }

    private scrollHandler = () => {
        // 25px threshold
        if (this.modalContainer.scrollTop >= this.modalContainer.scrollHeight - this.modalContainer.offsetHeight - 25) {
            this.isDown = true
        } else {
            this.isDown = false
        }
    }
}
