import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Component, Input, OnDestroy, OnInit } from '@angular/core'
import { ActivatedRoute } from '@angular/router'
import { find } from 'lodash'
import { of, Subject, Subscription } from 'rxjs'
import { flatMap, switchMap, tap } from 'rxjs/operators'
import { FeeType, ICurrency, IFee, IUser, Paginated, TransactionMethod } from '../../api-interfaces'
import { Currency } from '../../models/accounting/currency.model'
import { SessionService } from '../../services/session.service'

@Component({
    selector: 'term-fees',
    templateUrl: 'term-fees.component.html',
})
export class TermFeesComponent implements OnInit, OnDestroy {
    public fees: IFee[]
    public currency: ICurrency
    public currencies: ICurrency[] = []
    public fetchEvent = new Subject<void>()
    @Input()
    public user?: IUser
    public isLoading = false

    protected subscriptions = new Subscription()

    constructor(protected http: HttpClient, public session: SessionService, private route: ActivatedRoute) {}

    public ngOnInit(): void {
        this.http
            .get<Paginated<ICurrency>>('/currencies', {
                params: {
                    limit: 100 + '',
                },
            })
            .subscribe(response => {
                this.currencies = response.data
                this.currency = find(this.currencies, {
                    code: this.session.user ? this.session.user.preferredCurrency.code : 'USD',
                })!
                this.fetchEvent.next()
            })
        this.subscriptions.add(
            this.fetchEvent
                .pipe(
                    tap(() => {
                        this.isLoading = true
                    }),
                    switchMap(() => (this.session.isAuthenticated() ? this.session.userStream : of(undefined))),
                    flatMap(sessionUser => {
                        const params = {
                            currencyCode: this.currency.code,
                        }
                        const referrerId = this.route.snapshot.queryParamMap.get('referrerId')
                        const user = this.user || sessionUser
                        const activationToken = this.route.snapshot.queryParamMap.get('activationToken')
                        let headers = new HttpHeaders()
                        if (activationToken) {
                            headers = headers.set('X-Activation-Token', activationToken)
                        }
                        return user && user.id
                            ? this.http.get<IFee[]>(`/users/${user.id}/fee-schedule`, {
                                  params,
                                  headers,
                              })
                            : this.http.get<IFee[]>(`/fee-schedule`, {
                                  params: referrerId
                                      ? {
                                            ...params,
                                            referrerId,
                                        }
                                      : params,
                              })
                    })
                )
                .subscribe(fees => {
                    this.fees = fees
                    this.isLoading = false
                })
        )
    }
    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public getFee(type: FeeType, method: TransactionMethod): IFee | null {
        const found = find(this.fees, fee => fee.type === type && fee.method === method)
        if (!found || (!found.relative && !found.fixed)) {
            return null
        }
        return found
    }

    public convert(amount: string, baseCurrency: ICurrency): string {
        const base = this.currencies.find(currency => currency.code === baseCurrency.code)
        const counter = this.currencies.find(currency => currency.code === this.currency.code)
        if (!base || !counter) {
            return '0'
        }
        return Currency.convert(amount, base, counter)
    }

    public onCurrencyChange() {
        this.fees = []
        this.fetchEvent.next()
    }
}
