import { HttpClient, HttpHeaders } from '@angular/common/http'
import { AfterViewInit, Component, OnDestroy, OnInit, ViewChild } from '@angular/core'
import { NgForm, NgModel } from '@angular/forms'
import { ActivatedRoute, Router } from '@angular/router'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import formatDate from 'date-fns/format'
import parseISO from 'date-fns/parseISO'
import { omit, pick } from 'lodash'
import { of, Subscription } from 'rxjs'
import { flatMap } from 'rxjs/operators'
import { environment } from 'src/environments/environment'
import { BusinessDetails, IUser, Referrer, Setting, UserDetails } from '../common/api-interfaces'
import { countriesSortedByName } from '../common/countries'
import { AlertService } from '../common/services/alert.service'
import { LoginService } from '../common/services/login.service'
import { TermsModalComponent } from '../common/terms-modal/terms-modal.component'
import { formatMaskedDate } from '../common/user-details-form/user-details-form.component'
import { EmailVerificationModalComponent } from './email-verification-modal.component'
import { RegistrationNoticeComponent } from './registration-notice/registration-notice.component'

@Component({
    selector: 'registration',
    templateUrl: 'registration.component.html',
})
export class RegistrationComponent implements OnInit, OnDestroy, AfterViewInit {
    public countriesSortedByName = countriesSortedByName

    public activationToken?: string
    public user: Partial<IUser> & { password?: string } = {
        type: 'personal',
    }
    public details: Partial<UserDetails> = {}
    public business: Partial<BusinessDetails> = {}
    public birthday: string | null = ''

    @ViewChild('registrationForm')
    public registrationForm: NgForm
    public termsAccepted = false
    public hasReadTerms = false

    public isLoading = false
    public isInvitation = false
    public isRegistrationDisabled = false

    public requiredPasswordStrength = 60
    public passwordStrength = 0
    public externalId: string | undefined
    public referrerId: string | undefined
    public referrer: Referrer | null = null
    public appName = environment.appName
    public displayName = environment.displayName
    public referredByOptions = [
        `${this.translate.instant('registration.friend-relative')}`,
        `${this.translate.instant('registration.business-contact')}`,
        `${this.translate.instant('registration.corporate-service-provider')}`,
        `Authorised Partner of ${environment.displayName}`,
        'Other',
    ]
    public customReferredBy = ''

    private subscriptions = new Subscription()

    constructor(
        private router: Router,
        private route: ActivatedRoute,
        private http: HttpClient,
        public alertService: AlertService,
        private loginService: LoginService,
        private ngbModal: NgbModal,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.route.queryParamMap.subscribe(params => {
            this.activationToken = params.get('activationToken') || undefined
            Object.assign(this.user, {
                id: params.get('userId') || undefined,
                email: params.get('email') || undefined,
            })
            if (this.user.id && this.activationToken) {
                this.http
                    .get<IUser>(`/users/${this.user.id}`, {
                        headers: new HttpHeaders({ 'X-Activation-Token': this.activationToken }),
                    })
                    .subscribe(
                        user => {
                            Object.assign(this.user, pick(user, ['type', 'email', 'details']))
                            this.details = this.user.details || {}
                            this.business = this.user.details?.business || {}
                            this.birthday = this.details.birthday
                                ? formatDate(parseISO(this.details.birthday), 'ddMMyyyy')
                                : null
                            this.isInvitation = !!user.email
                        },
                        () => {
                            this.alertService.add({
                                severity: 'danger',
                                message: this.translate.instant('common.invitation-expired'),
                            })
                            return this.router.navigate(['/login'])
                        }
                    )
            }
            if (params.has('referrerId')) {
                this.referrerId = params.get('referrerId')!

                this.http.get<Referrer>(`/referrers/${this.referrerId}`).subscribe(
                    referrer => {
                        this.referrer = referrer
                    },
                    () => {
                        this.referrerId = undefined
                        this.referrer = null
                    }
                )
            }
            this.externalId = params.get('externalId') ?? undefined
        })
        this.subscriptions.add(
            this.http.get<Setting>(`/settings/registration-disabled`).subscribe(setting => {
                this.isRegistrationDisabled = setting && (setting.value as boolean)
            })
        )
    }

    public ngAfterViewInit(): void {
        setTimeout(() => {
            if (this.appName === 'sgpmx') {
                this.openNoticeModal()
            }
        }, 200)
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public submit(): void {
        if (!this.registrationForm.valid) {
            return
        }
        let headers = new HttpHeaders()
        if (!this.termsAccepted) {
            this.alertService.add({
                severity: 'danger',
                message: this.translate.instant('common.you-must-accept-terms'),
            })
            return
        }
        if (this.activationToken) {
            headers = headers.set('X-Activation-Token', this.activationToken)
        }
        if (this.details.referredBy === 'Other') {
            this.details.referredBy = this.customReferredBy
        }
        const body = {
            ...this.user,
            details: {
                ...this.details,
                birthday: this.birthday ? formatMaskedDate(this.birthday) : null,
                ...(this.user.type === 'business' ? { business: this.business } : {}),
            },
            externalId: this.externalId ?? undefined,
        }
        if (this.referrer) {
            body.referrer = { id: this.referrer.id } as Referrer
        }
        this.isLoading = true
        this.http
            .post<IUser>('/users', omit(body, ['id']), { headers })
            .pipe(
                flatMap(user =>
                    this.isInvitation
                        ? this.loginService.login({
                              password: this.user.password,
                              email: user.email,
                          })
                        : of(user)
                )
            )
            .subscribe(
                async user => {
                    if (this.isInvitation) {
                        await this.router.navigate(['/verify'])
                    } else {
                        this.reset()
                        const modal = this.ngbModal.open(EmailVerificationModalComponent, {
                            backdrop: 'static',
                            keyboard: false,
                            centered: true,
                        })
                        modal.componentInstance.email = user.email
                        await modal.result.then(() => this.router.navigate(['/login']))
                    }
                },
                error => {
                    this.isLoading = false
                    this.alertService.clear()
                    if (error.name === 'UniqueConstraintError') {
                        switch (error.errors[0]) {
                            case 'email must be unique':
                                this.alertService.add({
                                    severity: 'danger',
                                    message: this.translate.instant('common.email-already-in-use'),
                                })
                        }
                    }
                    throw error
                }
            )
    }

    public openTermsModal(): void {
        const termsModal = this.ngbModal.open(TermsModalComponent, {
            windowClass: 'modal-primary',
            backdrop: 'static',
            keyboard: false,
        })
        const termsModalComponent = termsModal.componentInstance as TermsModalComponent
        termsModalComponent.user = this.user as IUser
        termsModal.result
            .then(() => {
                this.termsAccepted = true
                this.hasReadTerms = true
            })
            .catch(() => {
                this.termsAccepted = false
            })
    }

    public onAcceptTerms(accepted: boolean, ngModel: NgModel): void {
        if (accepted && !this.hasReadTerms) {
            ngModel.control.markAsTouched()
            this.openTermsModal()
        }
    }

    public getLogo(): string {
        switch (environment.appName) {
            case 'etransfer':
                return '/img/brand/logo.svg'
            default:
                return '/img/brand/logo-white-horizontal.svg'
        }
    }

    public openNoticeModal(): void {
        this.ngbModal.open(RegistrationNoticeComponent, {
            windowClass: 'modal-primary',
        })
    }

    private reset(): void {
        this.user = {}
        this.registrationForm.resetForm()
        this.isLoading = false
        this.alertService.clear()
    }
}
