import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Component, Input, OnDestroy, OnInit } from '@angular/core'
import { ActivatedRoute } from '@angular/router'
import BigNumber from 'bignumber.js'
import { forkJoin, Observable, of, Subscription } from 'rxjs'
import { flatMap, map, switchMap, tap } from 'rxjs/operators'
import { FeesQuery } from 'src/app/store/fees/fees.query'
import { FeesService } from 'src/app/store/fees/fees.service'
import { FeeType, IFee, IUser, TransactionMethod } from '../../api-interfaces'
import { SessionService } from '../../services/session.service'

@Component({
    selector: 'sgpmx-term-fees',
    templateUrl: 'sgpmx-term-fees.component.html',
})
export class SgpmxTermFeesComponent implements OnInit, OnDestroy {
    @Input()
    public user: IUser
    public fees: IFee[] = []
    public isLoading = false

    private subscriptions = new Subscription()

    constructor(
        private session: SessionService,
        private route: ActivatedRoute,
        private http: HttpClient,
        private feesQuery: FeesQuery,
        private feesService: FeesService
    ) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            of(undefined)
                .pipe(
                    tap(() => {
                        this.isLoading = true
                    }),
                    switchMap(() => (this.session.isAuthenticated() ? this.session.userStream : of(undefined))),
                    flatMap(sessionUser => {
                        const referrerId = this.route.snapshot.queryParamMap.get('referrerId')
                        const activationToken = this.route.snapshot.queryParamMap.get('activationToken')
                        const user = this.user || sessionUser
                        let headers = new HttpHeaders()

                        if (activationToken) {
                            headers = headers.set('X-Activation-Token', activationToken)
                        }
                        return user && user.id
                            ? forkJoin([
                                  this.http.get<IFee[]>(`/users/${user.id}/fee-schedule`, {
                                      params: { currencyCode: 'AU' },
                                      headers,
                                  }),
                                  this.http.get<IFee[]>(`/users/${user.id}/fee-schedule`, {
                                      params: { currencyCode: 'AG' },
                                      headers,
                                  }),
                              ])
                            : forkJoin([
                                  this.http.get<IFee[]>(`/fee-schedule`, {
                                      params: referrerId
                                          ? {
                                                currencyCode: 'AU',
                                                referrerId,
                                            }
                                          : {
                                                currencyCode: 'AU',
                                            },
                                  }),
                                  this.http.get<IFee[]>(`/fee-schedule`, {
                                      params: referrerId
                                          ? {
                                                currencyCode: 'AG',
                                                referrerId,
                                            }
                                          : {
                                                currencyCode: 'AG',
                                            },
                                  }),
                              ])
                    })
                )
                .subscribe(([goldFees, silverFees]) => {
                    this.fees = [...goldFees, ...silverFees]
                    this.isLoading = false
                })
        )
        this.feesService.fetch()
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public getFee(type: FeeType, method: TransactionMethod, currencyCode: string): IFee | null {
        const found = this.fees.find(
            fee => fee.type === type && fee.method === method && fee.currency.code === currencyCode
        )
        if (!found || (!found.relative && !found.fixed)) {
            return null
        }
        return found
    }

    public getGst(): Observable<string> {
        return this.feesQuery
            .findByPrimaryAttributes({
                type: 'gst',
                method: 'internal',
                currencyCode: 'USD',
            })
            .pipe(map(gst => (gst ? `${new BigNumber(gst.relative || 0).times(100).toFixed(0)}%` : 'FREE')))
    }
}
