import { Injectable } from '@angular/core'
import { QueryEntity } from '@datorama/akita'
import { Observable, of } from 'rxjs'
import { flatMap, map } from 'rxjs/operators'
import { FeeType, IFee, TransactionMethod } from 'src/app/common/api-interfaces'
import { FeesState, FeesStore } from './fees.store'

@Injectable({ providedIn: 'root' })
export class FeesQuery extends QueryEntity<FeesState> {
    constructor(protected store: FeesStore) {
        super(store)
    }

    public findByPrimaryAttributes(query: {
        type: FeeType
        method: TransactionMethod
        currencyCode: string
    }): Observable<IFee | null> {
        return this.selectEntity(
            entity =>
                entity.type === query.type &&
                entity.method === query.method &&
                query.currencyCode === entity.currency.code
        ).pipe(
            flatMap(fee =>
                fee
                    ? of(fee)
                    : this.selectEntity(
                          entity =>
                              entity.type === query.type &&
                              entity.method === query.method &&
                              entity.currency.code === 'USD'
                      ).pipe(map(usdFee => usdFee ?? null))
            )
        )
    }
}
